import Vue from 'vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import router from '../router'


Vue.use(Vuex)
Vue.use(VueRouter)

export default new Vuex.Store({
  state: {
    movies: []
  },
  mutations: {
    ADD_MOVIE(state, payload) {
      state.movies.push({
        id: payload.id,
        name: payload.name,
        image: payload.image,
        type: payload.type,
      });
      localStorage.setItem("movies", JSON.stringify(state.movies));
      router.push({path: "/"})
    }
  },
  getters: {
    lastId(state) {
      if (state.movies.length) {
        return state.movies[state.movies.length - 1].id + 1;
      } else {
        return 0;
      }
    }
  }
})